.wrapper {
    width: 100%;
    height: var(--default-header-height);
    //box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    display: flex;
    justify-content: center;
}

.inner {
    height: 100%;
    width: var(--default-content-width);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.actions {
    display: flex;
}

.logo {
    display: flex;
    cursor: pointer;
}
